<template>
  <div class="add-bout-dialog">
    <el-dialog
      title=""
      :visible="addBoutVisible"
      width="30%"
      center
      @close="addBoutCancel"
    >
      <el-row>
        <el-col :span="2">
          <i class="el-icon-warning fontsize-20"></i>
        </el-col>
        <el-col :span="22">
          <p
            class="content-text"
            v-for="(item, index) in contentText"
            :key="'contentText' + index"
          >
            {{ item }}
          </p>
        </el-col>
      </el-row>
      <div class="reason-box">
        <p class="title"></p>
        <el-input
          type="textarea"
          maxlength="100"
          show-word-limit
          placeholder=""
          v-model="reason"
        >
        </el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click="addBoutCancel">
          
        </el-button>
        <el-button size="small" type="primary" @click="addBoutConfirm">
          
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  props: {
    addBoutVisible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      reason: '',
      contentText: [
        '1',
        '2',
        '3'
      ]
    }
  },
  methods: {
    addBoutConfirm() {
      if (!this.reason) {
        this.$message.warning('')
        return
      }
      this.$emit('addBoutConfirm', this.reason)
    },
    addBoutCancel() {
      this.$emit('addBoutCancel')
    }
  }
}
</script>
<style lang="scss" scope>
.add-bout-dialog {
  .content-text {
    line-height: 18px;
  }
  .reason-box {
    margin-top: 15px;
    .title {
      margin: 5px 0 10px;
    }
  }
}
</style>
